
<template>
  <section>
    <div>
    <div  class="banner" v-if="users.length > 0">
      <div class="banner-content">
        <div class="qoute-contain">
          <p>{{ users[currentIndex].quotes }}</p>
        </div>
        <div class="name-design">
           <div class="name">
            <p>{{ users[currentIndex].name }}</p>
           </div>
           <div class="designation">
            <p>{{ users[currentIndex].designation }}</p>
           </div>
        </div>
      </div>
      <div class="image-container">
        <img :src="require(`@/assets/images/${users[currentIndex].image}`)" :alt="users[currentIndex].name"/>
           <div class="button-container">
               <button class="prev-button" @click="prevUser" :disabled="currentIndex === 0">
                 <img src="@/assets/images/icon-prev.svg"/>
                 </button>
               <button class="next-button" @click="nextUser" :disabled="currentIndex === users.length - 1"> 
                <img src="@/assets/images/icon-next.svg"/>
                </button>
           </div>
      </div>
    </div>
  </div>
  </section>
</template>

<script>
export default {
  name:'slideBanner',
  data() {
    return {
      users: [
        {
          name: 'Tanya Sinclair',
          designation: 'UX Engineer',
          image: 'image-tanya.jpg', 
          quotes: ' “ I’ve been interested in coding for a while but never taken the jump, until now. I couldn’t recommend this course enough. I’m now in the job of my dreams and so excited about the future. ” '
        },
        {
          name: 'John Tarkpor',
          designation: 'Junior Front-end Developer',
          image: 'image-john.jpg', 
          quotes: '“ If you want to lay the best foundation possible I’d recommend taking this course. The depth the instructors go into is incredible. I now feel so confident about starting up as a professional developer. ” '
        }
      ],
      currentIndex: 0
    };
  },
  methods: {
    nextUser() {
      if (this.currentIndex < this.users.length - 1) {
        this.currentIndex += 1;
      }
      
    },
    prevUser() {
      if (this.currentIndex > 0) {
        this.currentIndex -= 1;
      }
    }
  }
};
</script>

